.footer {
	@media only screen and (max-width: 980px) {
		padding-left: 12.5%;
		padding-right: 12.5%;
		text-align: center;
	}
	@media only screen and (min-width: 980px) {
		padding-left: 25%;
		padding-right: 25%;
	}
	background: #171717;
	color: #737373;
	padding-top: 40px;
	.fcontent {
	font-size: 14px;
	}
	.icon {
	font-size: 2em;
	text-align: center;
	}
	.image {
	height: 2em;
	}
	.largeicon {
	font-size: 8em;
	text-align: center;
	}
	.largeimage {
	height: 8em;
	}
}